<template lang="html">
  <div class="deposit-form-checked">
    <!--  -->
    <FilterArea />
    <!--  -->
    <div class="table-list">
      <!--  -->
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index" />

        <el-table-column prop="username" label="" />

        <el-table-column prop="nickname" label="" />

        <el-table-column prop="gameName" label="" />

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.type | depositDircetion}}</span>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.money | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.status | depositStatus}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="loanName" label="" />

        <el-table-column prop="remark" label="" />

        <el-table-column prop="operations" label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="showDialog(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <BasePagination
        @on-change="handlePaginationChange"
        :pageTotal="pageTotal"
        :requestParams="{ neStatus: 0 }"
        httpURL="fetchBalanceChangeList"
      />
    </div>
    <!--  -->
    <DialogDepositForm :formData="currentItem" :unchecked="false" ref="dialogDepositForm" />
  </div>
</template>

<script>
import FilterArea from '@/components/others/FilterArea'
import BasePagination from '@/components/base/BasePagination'
import DialogDepositForm from './components/DialogDepositForm'

export default {
  name: 'DepositFormChecked',
  components: {
    FilterArea,
    BasePagination,
    DialogDepositForm
  },
  data () {
    return {
      tableData: [],
      pageTotal: 0,
      currentItem: {}
    }
  },
  created () {
    this.fetchBalanceChangeList()
  },
  methods: {
    // 
    showDialog (obj) {
      this.currentItem = obj
      this.$refs.dialogDepositForm.toggleDialogVisible(true)
    },
    // 
    handlePaginationChange (data) {
      this.tableData = data
    },
    fetchBalanceChangeList () {
      this.$httpAPI.fetchBalanceChangeList({ params: { neStatus: 0, pageNo: 1, pageSize: 10 } }).then(response => {
        this.tableData = response.data.data
        this.pageTotal = response.data.amount
      }).catch(error => console.log(error))
    }
  }
}
</script>
