      </el-form>
      <span v-if="unchecked" slot="footer">
        <el-button @click="changeStatus(2)" type="primary" size="small"></el-button>
        <el-button @click="changeStatus(3)" type="danger" size="small"></el-button>
        <el-button @click="changeStatus(1)" size="small"></el-button>
      </span>
